
import React from 'react';
import styles from './css/styles.css';

class Header extends React.Component{
    componentDidMount(){
        this.props.ele && this.props.ele(this.refs.fixed)
        // console.log(this.props.ele)
    }
    render(){
        return (
            <header ref='fixed'>
                <div className={styles.back} onClick={this.props.backFn}>
                    &#xe615;
                </div>
                <h2>{this.props.title}</h2>
                {
                    this.props.children
                }
            </header>
        )
    }
}
export default Header;


