
import React from 'react';
import styles from './css/content.css';
class Content extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            data:[]
        }
    }
    componentWillReceiveProps(nextProps){
        const data = nextProps.data;
        this.setState({
            data: data,
            list: nextProps.list,
            len: data.list_product.length,
            re_tit: nextProps.list[0].goodsortname[0]
        });
    }
    componentDidMount() {
        fetch('http://692060.ichengyun.net/api/csair/holdaylist.php').then(res => res.json()).then(data => {
                this.setState({
                    data:data,
                    list: data.list_product,
                    len: data.list_product.length,
                    re_tit: data.list_product[0].goodsortname[0]
                });
                sessionStorage.data = JSON.stringify(data);
            })
        
    } 
    render(){
        return (
            <div className={styles.content}>
                <div className={styles.recommend}>
                    <div className={styles.re_tit}>
                        <p>
                            <i>&#xe65a;</i>
                            {this.state.re_tit}
                        </p>
                    </div>
                    {
                        this.state.list && this.state.list.map( (item,index) => {
                            return index < 1 && <div className={`${styles.clearfix} ${styles.recommend_box}`} key={index}>
                                <span className={styles.img} style={{ backgroundImage: `url(http://manager.csairholiday.com/${item.mainPhotoPath})`}}>
                                
                                </span>
                                <div className={styles.text}>
                                    <h5>{item.goodsname.substr(0,32)}</h5>
                                    <p>
                                        {
                                            item.goodstag && <i>{item.goodstag ? this.state.data.goodSort[item.goodstag].substr(7) : ''}</i>
                                        }
                                        <i>{this.state.data.goodSort[item.goodstag].substr(7)}</i>
                                        <span>￥{item.group_original_price}</span>
                                    </p>
                                    <div>
                                        <em>已有<i>{item.has_sold_num}</i>人购买</em>
                                        <span>￥{item.group_price}</span>
                                    </div>
                                </div>
                            </div>
                        })
                        
                    }
                    <div className={styles.numLine}>
                        <div></div>
                        <p>共有<span>{this.state.len}</span>条旅游产品信息</p>
                    </div>
                </div>
                <div>
                    {
                        this.state.list && this.state.list.map((item, index) => {
                            return <div className={`${styles.clearfix} ${styles.item}`} key={index}>
                                <span className={styles.img} style={{ backgroundImage: `url(http://manager.csairholiday.com/${item.mainPhotoPath})` }}>

                                </span>
                                <div className={styles.text}>
                                    <h5>{item.goodsname.substr(0,32)}</h5>
                                    <p>
                                        <i>{item.goodsortname[0]}</i>
                                        {
                                            item.goodstag && <i>{item.goodstag ? this.state.data.goodSort[item.goodstag].substr(7) : ''}</i>
                                        } 
                                        <span>￥{item.group_original_price}</span>
                                    </p>
                                    <div>
                                        <em>已有<i>{item.has_sold_num}</i>人购买</em>
                                        <span>￥{item.group_price}</span>
                                    </div>
                                </div>
                            </div>
                        })
                    }
                </div>
            </div>
        )
    }
}
export default Content;


