
import React from 'react';
import style from'./css/footer.css';
import CityData from '../CityData';
import Pop from './Pop';
import Product from '../Product';

class Footer extends React.Component{
    constructor(){
        super();
        this.state = {
            visible: 0,
            show:true
        }
    }
    // componentWillMount() {
    //     window.addEventListener("scroll", function (e) {
    //         alert("我监听到了浏览器的返回按钮事件啦");//根据自己的需求实现自己的功能
    //     }, false);
    // }
    
    toggle = (index) => {
        this.setState({
            visible: index,
            show:!this.state.show
        });
        this.state.show ? document.body.style.position = 'fixed' : document.body.style.position = '';
    }
    city = (d) => {
        this.setState({ city: d })
        this.toggle();
    }
    render(){
        return (
            <footer>
                <div className={style.foot}>
                    <div onClick={() => this.toggle(1)}>
                        <dl>
                            <dt>&#xe600;</dt>
                            <dd>产品类型</dd>
                        </dl>
                    </div>
                    <div>
                        <dl onClick={() => this.toggle(2)}>
                            <dt>&#xe688;</dt>
                            <dd>出发城市</dd>
                        </dl>
                    </div>
                    <div>
                        <dl onClick={() => this.toggle(3)}>
                            <dt>&#xe63b;</dt>
                            <dd>目的地</dd>
                        </dl>
                    </div>
                    <div>
                        <dl onClick={() => this.toggle(4)}>
                            <dt>&#xe66f;</dt>
                            <dd>筛选</dd>
                        </dl>
                    </div>
                </div>
                <Pop visible={this.state.visible} popid={1} >
                    <Product visible={this.state.visible} close={this.toggle} listid={1} title={'产品类型'} />
                </Pop>
                <Pop visible={this.state.visible} popid={2} >
                    <CityData visible={this.state.visible} close={this.toggle} city={this.city} title={'出发城市选择'} listid={2} />
                </Pop>
                <Pop visible={this.state.visible} popid={3} >
                    <CityData visible={this.state.visible} close={this.toggle} city={this.city} title={'目的地选择'} listid={3} />
                </Pop>
                <Pop visible={this.state.visible} popid={4} >
                    <Product visible={this.state.visible} close={this.toggle} listid={4} title={'筛选'} />
                </Pop>
                
                    
            </footer>
        )
    }
}
export default Footer;


