
import React from 'react';
import styles from './css/Filter.css';


class Filter extends React.Component{
    constructor(props){
        super(props);
        this.state = {
            data:[
            '最新发布',
            '价格 低→高',
            '价格 高→低',
            '销量 高→低',
            '人气 高→低'
            ],
            index: 0,
            tit: '最新发布',
            condition:false,
            sort: [
                {
                    logo: 'publishdate',
                    sort: 'asc'
                },{
                    logo: 'group_price',
                    sort: 'asc'
                }, {
                    logo: 'group_price',
                    sort: 'desc'
                }, {
                    logo: 'has_sold_num',
                    sort: 'desc'
                }, {
                    logo: 'clicknum',
                    sort: 'asc'
                }
            ]
        }
        
    }
    
    condition = () => {
        this.setState({ condition:!this.state.condition})
        !this.state.condition ? document.body.style.position = 'fixed' : document.body.style.position = ''
    }
    active = (index,tit) => {
        tit = tit.replace(' ','由').replace('→','至')
        this.setState({
            index:index,
            tit:tit
        })
        this.condition();
        this.props.sort(this.state.sort[index]);
    }
    render(){
        return (
            <div className={styles.filter}>
                <h5 onClick={this.condition} className={this.state.condition ? styles.tran : ''}>
                    <i>&#xe7cb;</i> 排序条件: {this.state.tit} 
                    <em>&#xe7cc;</em>
                </h5>
                {
                    this.state.condition && <div className={this.state.condition ? styles.conditionShow : styles.conditionHide}>
                        <ul>
                            {
                                this.state.data.map((item, index) => {
                                    return <li className={this.state.index === index ? styles.active : ''} onClick={() => this.active(index, item)} key={index}>
                                        <span>&#xe626;</span>
                                        {item}
                                    </li>
                                })
                            }
                        </ul>
                    </div>
                }
            </div>
        )
    }
}
export default Filter;


