
import React from 'react';
import styles from './css/Propduct.css';

class Product extends React.Component {
    constructor(props){
        super(props);
        const data = [
            [
                {
                    num:516,
                    txt: '全部'
                },
                {
                    num:123,
                    txt: '境外游'
                },
                {
                    num:123,
                    txt: '境内游'
                },
                {
                    num:35,
                    txt: '周边游'
                },
                {
                    num:195,
                    txt: '当地玩乐'
                },
                {
                    num:51,
                    txt: '特卖'
                },
                {
                    num:7,
                    txt: '会员专享'
                },
                {
                    num:189,
                    txt: '免税品'
                },
            ],[
                {
                    num: '不限'
                },
                {
                    num: '2日'
                },
                {
                    num: '3日'
                },
                {
                    num: '4日'
                },
                {
                    num: '5日'
                },
                {
                    num: '6日'
                },
                {
                    num: '7日'
                },
                {
                    num: '大于7日'
                },
            ]
        ]
        this.state = {
            data:data,
            product:[true],
            filter:[],
            time:[],
            pOld: 0,
            fOld: 0,
            tOld:0
        }
    }
    product = (x) => {
        let arr = this.state.product;
        arr[this.state.pOld] = false;
        arr[x] = true;
        this.setState({
            propduct: arr,
            pOld: x
        })
    }
    filter = (x) => {
        let arr = this.state.filter;
        console.log(this.state.fOld)
        console.log(arr)
        arr[this.state.fOld] = false;
        arr[x] = true;
        this.setState({
            filter: arr,
            fOld: x
        })
    }
    time = (x) => {
        let arr = this.state.time;
        console.log(this.state.tOld)
        console.log(arr)
        arr[this.state.tOld] = false;
        arr[x] = true;
        this.setState({
            time: arr,
            tOld: x
        })
    }
    reset = () => {
        this.setState({
            filter: [],
            time: []
        })
    }
    render() {
        return (
            <div className={`${styles.mask} ${this.props.visible === this.props.listid ? styles.disb : styles.disn}`}>
                {
                    this.props.visible === this.props.listid && <div className={styles.wrap}>
                        <h5 className={styles.btns}>
                            <span onClick={this.props.close} className={styles.no} >取消</span>
                            <p>{this.props.title}</p>
                            <span onClick={this.props.close} className={styles.ok} >确定</span>
                        </h5>
                        <div className={styles.bottom}>
                            {
                                this.props.title === '产品类型' && <div>
                                    <h6 className={styles.tit}>
                                        <em></em>
                                        <span>{`当前条件下，共有${516}条产品`}</span>
                                    </h6>
                                    <div className={styles.con}>
                                        {
                                            this.state.data[0].map((item, index) => {
                                                return <p className={this.state.product[index] ? styles.active : ''} onClick={ () => this.product(index)}>
                                                    <i>{item.txt}</i>
                                                    <span>{item.num}</span>
                                                </p>
                                            })
                                        }
                                    </div>
                                </div>
                            }
                            {
                                this.props.title === '筛选' && <div>
                                    <div className={styles.shaixuan}>
                                        <h6 className={styles.tit}>
                                            {`当前条件下，共有${43}条产品`}
                                        </h6>
                                        <h5>游玩天数</h5>
                                        <div className={styles.con} id={styles.conMin}>
                                            {
                                                this.state.data[1].map((item, index) => {
                                                    return <p key={index} onClick={() => this.filter(index)} className={this.state.filter[index] ? styles.active : ''}>
                                                        <span>{item.num}</span>
                                                    </p>
                                                })
                                            }
                                        </div>
                                    </div>
                                    <h5>自定义出发时间</h5>
                                    <div className={styles.time}>
                                        <div>
                                            <span onClick={ () => this.time(0)} className={this.state.time[0] ? styles.active : ''}>最早出发时间</span>
                                            <span onClick={ () => this.time(1)} className={this.state.time[1] ? styles.active : ''}>最晚出发时间</span>
                                        </div>
                                    </div>
                                    <div className={styles.reset}>
                                        <button onClick={this.reset}>重置</button>
                                    </div>
                                </div>
                            }
                        </div>
                    </div>
                }
            </div>
        );
    }
}

export default Product;


