
import React from 'react';
import styles from './css/CityData.css';
import Header from '../Header';
class CityData extends React.Component{
    constructor(props){
        super(props);
        const zm = ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'J', 'K', 'L', 'M', 'N', 'P', 'Q', 'R', 'S', 'T', 'U', 'W', 'X', 'Y', 'Z'];
        const city = {
            domestic:[
                {
                    cityname: '北京'
                },
                {
                    cityname: '上海'
                },
                {
                    cityname: '广州'
                },
                {
                    cityname: '深圳'
                },
                {
                    cityname: '天津'
                },
                {
                    cityname: '成都'
                },
                {
                    cityname: '大连'
                },
                {
                    cityname: '杭州'
                },
                {
                    cityname: '三亚'
                },
                {
                    cityname: '重庆'
                },
            ],
            international:[
                {
                    cityname: '新加坡'
                },
                {
                    cityname: '纽约'
                },
                {
                    cityname: '首尔'
                },
                {
                    cityname: '曼谷'
                },
                {
                    cityname: '普吉岛'
                },
                {
                    cityname: '东京'
                },
                {
                    cityname: '清迈'
                },
                {
                    cityname: '兰卡威'
                },
                {
                    cityname: '巴厘岛'
                },
                {
                    cityname: '暹粒'
                },
            ],
            gangao:[
                {
                    cityname: '葵青区'
                },
                {
                    cityname: '北区'
                },
                {
                    cityname: '西贡区'
                },
                {
                    cityname: '沙田区'
                },
                {
                    cityname: '大埔区'
                },
                {
                    cityname: '荃湾区'
                },
                {
                    cityname: '屯门区'
                },
                {
                    cityname: '元朗区'
                }
            ]
        }
        this.state = {
            data:[],
            cData: city,
            zm:zm,
            logo:'A',
            region:[
                {
                    num: true,
                    text: 'domestic'
                },{
                    num: false,
                    text: 'international'
                },{
                    num: false,
                    text: 'gangao'
                }
            ],
            old: 0,
            ele:null
        };
    }
    componentDidMount() {
        
        
        this.refs.ele.addEventListener("scroll", (e) => {
            let aaa = this.state.ele,
                height = 174 - e.target.scrollTop / 4;

            aaa.style.cssText = `height:${height}px;overflow:hidden`;
            if(e.target.scrollTop > 174){
                aaa.className = styles.fixed
                aaa.style.cssText = '';
            }else{
                aaa.className = '';
            }
        }, false);
    }
    componentWillReceiveProps = (nextProps) =>{
        //子组件接受新的props的时候自动调用
        
        if(nextProps.visible === nextProps.listid){
            try{
                this.setState({data:JSON.parse(localStorage.citydata)});
            }catch(e){
                fetch('http://692060.ichengyun.net/api/csair/citylist.php').then(res => res.json()).then(data => {
                    this.setState({ data: data.citydata });
                    localStorage.citydata = JSON.stringify(this.state.data);
                })
            }
        }
    }
    ele = (x) => {
        this.setState({
            ele: x
        })
    }
    demo = (d) => {
        this.setState({logo:d});
    }
    region(index){
        let arr = this.state.region;
        arr[this.state.old].num = false;
        arr[index].num = true;
        this.setState({
            old:index,
            region: arr
        })
    }
    render(){
        const setData = {
            backFn: this.props.close,
            title: this.props.title
        }
        return (
            <div className={this.props.visible === this.props.listid ? styles.show : styles.hide} ref='ele'>
                {
                    this.props.visible === this.props.listid && <div>
                        <Header {...setData} ele={this.ele}>
                            <div className={styles.search_wrap}>
                                <input type='text' placeholder='关键字/简拼/中/英/三字码' className={styles.search} />
                            </div>
                        </Header>
                        <div className={styles.now_wrap}>
                            <h6 className={styles.tit}>
                                <em></em>
                                <span>最近选择</span>
                            </h6>
                            <div className={`${styles.now} ${styles.clearfix}`}>
                                <i>当前位置</i>
                            </div>
                        </div>
                        <div>
                            <div className={styles.regionNav}>
                                <div onClick={() => this.region(0)} className={this.state.region[0].num ? styles.active : ''} >国内</div>
                                <div onClick={() => this.region(1)} className={this.state.region[1].num ? styles.active : ''} >国际</div>
                                <div onClick={() => this.region(2)} className={this.state.region[2].num ? styles.active : ''} >港澳台</div>
                            </div>
                            <div className={styles.hot_city}>
                                <h6 className={styles.tit}>
                                    <em></em>
                                    <span>热门城市</span>
                                </h6>
                                <div>
                                    {
                                        this.state.cData[this.state.region[this.state.old].text].map((item, index) => {
                                            return <span key={index} onClick={() => this.props.city(item.cnname)}>{item.cityname}</span>
                                        })
                                    }
                                </div>
                            </div>
                            <div className={styles.allCity}>
                                <h6 className={styles.tit}>
                                    <em></em>
                                    <span>字母选择</span>
                                </h6>
                                {
                                    this.props.visible === this.props.listid && this.state.zm.map((items, index) => {
                                        return <div key={index} className={styles.wrap}>
                                            <p onClick={() => this.demo(items)} className={styles.cityTit} id={this.state.logo === items ? styles.cityShow : styles.cityHide}>{items}</p>
                                            <div>
                                                {
                                                    this.state.data.map((item, inx) => {
                                                        return items === item.initail && <div key={inx} onClick={() => this.props.city(item.cnname)}>{item.cnname}</div>
                                                    })
                                                }
                                            </div>
                                        </div>
                                    })
                                }
                            </div>
                        </div>
                    </div>
                }
            </div>
        )
    }
}
export default CityData;


