
import React from 'react';
import '../Public/reset.css';
import Header from '../Header';
import Filter from '../Filter';
import Content from '../Content';
import Footer from '../Footer';

class Common extends React.Component {
    constructor(){
        super();
        this.state = {
            data:[],
            list:[]
        }
    }
    componentWillMount(){
        if (sessionStorage.data) return false;
        try{
            this.setState({
                data: JSON.parse(sessionStorage.data),
                list: JSON.parse(sessionStorage.data).list_product
            })
        }catch(e){
            console.log(e);
        }
    }
    click = (obj) => {
        console.log(obj)
        const data = JSON.parse(sessionStorage.data)
        let list = data.list_product
        this.sort(list, obj.logo, obj.sort);
        this.setState({
            data:data
        })
    }
    sort = (arr,x,sc) => {
        if(sc === 'asc'){
            this.setState({
                list: arr.sort( (a,b) => {
                    return a[x] - b[x];
                })
            }) 
        }else if(sc === 'desc'){
            this.setState({
                list: arr.sort((a, b) => {
                    return b[x] - a[x];
                })
            }) 
        }
        
    }
    render() {
        return (
            <div>
                <Header title='南航旅游' />
                <Filter sort={this.click}/>
                <Content data={this.state.data} list={this.state.list}/>
                <Footer />
            </div>
        )
    }
}

export default Common


