 import React from 'react';
 import styles from './css/pop.css';
 class Pop extends React.Component {
     
    
     render() {
         return (
             <div className={this.props.visible === this.props.popid ? styles.block : styles.none}>
                 {this.props.children}
             </div>
            
         )
     }
 }
 
 export default Pop;


